import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/pages/HomeView.vue'
import Carregamento from '../views/pages/Carregamento.vue'
import Aula from '../views/pages/RTF.vue'
import Biblioteca from '../views/pages/Historico.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect:"/home"
  },
  {
    path: '/home',
    name: 'home',
    component: HomeView
  },
  { 
    path: '/carregamento',
    name: 'carregamento',
    component: Carregamento
  },
  {
    path: '/historico',
    name: 'historico',
    component: Biblioteca,
    children:[
     
        {
          path:'carregamento',
          name:'carregamentos',
          component: ()=>import('../views/pages/Historico/Carregamento.vue')
          
        },
        {
          path:'acesso',
          name:'acesso',
          component: ()=>import('../views/pages/Historico/Acesso.vue')
          
        },
        {
          path:'descarregados',
          name:'descarregados',
          component: ()=>import('../views/pages/Historico/Descarregado.vue')
          
        }
    ]
  },
  {
    path: '/remoteFileTransfer',
    name: 'remoto',
    component: Aula
  },

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})

export default router
