<template>

  <div style="height: 100px; position: relative">
    <v-fab-transition>

      <v-btn
        color="pink"
        v-show="show"
        dark
        absolute
        top
        right
        @click="lock = !lock"
        fab>
        <v-icon v-if="lock">
            mdi-lock
          </v-icon>
          <v-icon v-else>
            mdi-lock-open-variant
          </v-icon>
      </v-btn>
    </v-fab-transition>
  </div>
  </template>
  
  <script>
  export default {
    data() {
        return {
            show:true,
            lock: false
        }
    },
    watch:{
        '$route.name':{
            handler(name){

                switch (name) {
                    case 'historico':

                        this.show = true
                        
                     break;
                
                    case 'remoto':

                    this.show = true


                    break;
                    default:

                    this.show = false


                    break;
                }


            },
            immediate:true
        },
    }


    


  };
  </script>
  